<template>
    <div id="my-search">
        <header>
            <el-input v-model="keyword"
                icon="search"
                placeholder="search for..."
                @input="throll(querySearch, 300)"></el-input>
            <el-button type="text"
                @click="goBack">取消</el-button>
        </header>
        <div v-show="!relatedBlog.length">
            <p class="no-relevant">
                No relevant content.
            </p>
        </div>
        <div v-for="item in relatedBlog"
            :key="item.index">
            <transition name="el-fade-in-linear">
                <p class="search-content"
                    v-show="finishSearch"
                    @click="goToBlog(item.index)">{{item.value}}</p>
            </transition>
        </div>
    </div>
</template>
<script>
export default {
    data () {
        return {
            relatedBlog: [],
            keyword: '',
            finishSearch: false,
            searchTimer: null
        };
    },
    methods: {
        // 去空格转为小写搜索
        formatSearch (key) {
            return key.toLowerCase().replace(/\s/g, '')
        },
        // 操作节流函数
        throll (fn, delay) {
            const that = this
            clearTimeout(that.searchTimer)
            that.searchTimer = setTimeout(fn, delay)
        },
        // 模糊搜索，搜索框提示内容
        querySearch () {
            const that = this
            const key = that.formatSearch(that.keyword)
            if (key === '') {
                that.relatedBlog = []
                that.finishSearch = false
                return false
            }
            let _arr = []
            that.$store.state.initialBlogData.forEach((e, i) => {
                if (that.formatSearch(e.brief).indexOf(key) !== -1 || that.formatSearch(e.title).indexOf(key) !== -1) {
                    _arr.push({ value: e.title, index: e.url })
                }
            });
            setTimeout(() => {
                that.relatedBlog = _arr
                that.finishSearch = true
            }, 500);
        },
        // 前往所搜索的 blog
        goToBlog (url) {
            this.$store.state.searchVis = true

            this.$router.push('/blog/blog_' + url)
        },
        // 回到上一页
        goBack () {
            this.$router.go(-1)
            this.$store.state.searchVis = true
        }
    }
};
</script>
<style lang="scss" scoped>
#my-search {
    & {
        margin: -50px auto -40px auto;
        width: 100%;
    }

    header {
        & {
            padding: 10px;
        }

        .el-input {
            width: calc(100% - 48px);
        }

        .el-input__inner {
            border: none;
            -webkit-border-radius: 0;
            -moz-border-radius: 0;
            border-radius: 0;
            background: #f9fafc;
            border-bottom: 1px solid #bfcbd9;
        }
    }

    .el-button.el-button--text {
        float: right;
    }

    .no-relevant {
        text-align: center;
        color: #8492a6;
    }

    .search-content {
        font-size: 14px;
        color: #475669;
        border-bottom: 1px solid #d3dce6;
        padding: 10px 0 10px 4px;
        margin: 0 10px;
    }
}
</style>



